import React from 'react'
import { ReactElement } from 'react'

import { SubjectSummary } from '../../../../components/subject/SubjectSummary'
import { SubjectContent } from '../../../../components/subject/SubjectContent'
import { SubjectTitle } from '../../../../components/subject/SubjectTitle'
import { SubjectKeyword } from '../../../../components/subject/SubjectKeyword'
import { SubjectIdentifier } from '../../../../components/subject/SubjectIdentifier'
import { Subject } from '../../../../components/subject/Subject'

export function Kemet () : ReactElement {
  return (
    <Subject>
      <SubjectIdentifier>magic-runes-stars-kemet</SubjectIdentifier>
      <SubjectKeyword>rune</SubjectKeyword>
      <SubjectTitle>Kémèt</SubjectTitle>
      <SubjectSummary>
      </SubjectSummary>
      <SubjectContent>
        <p className='text-center'>
          <img src='./images/runic-kemet.svg' width='200' />
        </p>

        <p className='text-center ipa'>
          [ke.mɛt]
        </p>

        <p className='text-center ipa'>
          Kémèt aussi appellée l'étoile des flammes est la rune permettant de
          former des évocations liées à l'élément du feu.
        </p>

        <table className='table-1d table-vertical'>
          <thead>
            <tr>
              <th style={{width: '100px'}}> Nature </th>
              <th style={{width: '300px'}}> Synonyme(s) </th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td className='text-center'> Sujet </td>
              <td> Le feu, la chaleur. </td>
            </tr>
            <tr>
              <td className='text-center'> Objet </td>
              <td> Le feu, la chaleur. </td>
            </tr>
            <tr>
              <td className='text-center'> Adjectif </td>
              <td> Chaud, brûlant. </td>
            </tr>
            <tr>
              <td className='text-center'> Verbe </td>
              <td>
                <p>
                  <strong>Aigrettes.</strong> La tête du personnage est montée
                  de deux grandes aigrettes de plumes qui de concert avec un
                  rafinement de son instinct lui permette quasiment de voir
                  grâce au son.
                </p>

                <p>
                  Perception +8 quand il s'agit d'entendre.
                </p>

                <p>
                  Esquive +6
                </p>
              </td>
            </tr>
            <tr>
              <td className='text-center'> Adverbe </td>
              <td>
                <p>
                  <strong>Danse du rapace.</strong> L'instinct du personnage est
                  tellement affuté qu'il est capable d'esquiver gratuitement une
                  attaque tous les deux rounds.
                </p>
              </td>
            </tr>
          </tbody>
        </table>
      </SubjectContent>
    </Subject>
  )
}
